<template>
    <div class="rating_page">
        <header id="head_top">
            <section class="head_goback" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </section>
            <section class="title_head ellipsis">
                        <span class="title_text">
                            在线支付
                        </span>
            </section>
        </header>
        <section class="show_time_amount">
            <section>
                <header class="time_last">
                    剩余时间
                </header>
                <p class="time">00:{{minutes}}:{{seconds}}</p>
            </section>
        </section>
        <div class="pay_way">选择支付方式</div>
        <section class="pay_way_list">
            <section class="pay_item">
                <div class="pay_icon_contaienr">
                    <img src="../assets/2.jpg" alt="">
                    <span>支付宝</span>
                </div>
                <i class="el-icon-success" @click="show=true" :class="{color:show}"></i>
            </section>
            <section class="pay_item">
                <div class="pay_icon_contaienr">
                    <img src="../assets/1.jpg" alt="">
                    <span>微信</span>
                </div>
                <i class="el-icon-success" @click="show=false" :class="{color:!show}"></i>
            </section>
            <p class="determine" @click="confirmPayment">
                确认支付
            </p>
        </section>
        <div class="alet_container" v-if="show1">
            <section class="tip_text_container">
                <div class="tip_icon">
                   <span></span>
                    <span></span>
                </div>
                <p class="tip_text">支付超时</p>
                <p class="confrim">确认</p>
            </section>
        </div>
        <div class="alet_container" v-if="show2">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">暂不开放支付功能</p>
                <p class="confrim" @click="show2=false">确认</p>
            </section>
        </div>
        <div class="alet_container" v-if="!show3">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">当前环境无法支付，请打开官方APP进行支付</p>
                <p class="confrim" @click="confirmBtn">确认</p>
            </section>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Payment",
        data(){
            return{
                show:true,
                show1:false,
                minutes: 15,
                seconds: '00'/1,
                show2:true,
                show3:true
            }
        },
        created(){
            this.add();
        },
        methods:{
            goBack(){
                this.$router.go(-1)
            },
            num(n) {
                return n < 10 ? '00' + n : '' + n
            },
            add(){
                var _this = this
                var time = window.setInterval(function () {
                    if (_this.seconds === 0 && _this.minutes !== 0) {
                        _this.seconds = 59
                        _this.minutes -= 1
                    } else if (_this.minutes === 0 && _this.seconds === 0) {
                        _this.seconds = '00'-0
                        window.clearInterval(time)
                    } else {
                        _this.seconds -= 1
                    }
                }, 1000)

            },
            confirmPayment(){
                this.show3 = false;
            },
            confirmBtn(){
                this.show3 = true;
                this.$router.push({
                    path:"/order",
                })
            }
        },
        watch: {
            second: {
                handler (newVal) {
                    this.num(newVal)
                }
            },
            minute: {
                handler (newVal) {
                    this.num(newVal)
                }
            }
        },
        computed: {
            second: function () {
                return this.num(this.seconds)
            },
            minute: function () {
                return this.num(this.minutes)
            }
        }
    }
</script>

<style scoped>
    #head_top{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    .head_goback{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        line-height: 2.2rem;
        margin-left: .4rem;
        color: #fff;
    }
    .title_head{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .title_text {
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .rating_page{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #f5f5f5;
        z-index: 204;
        padding-top: 1.95rem;
    }
    .show_time_amount{
        background-color: #fff;
        padding: .7rem;
        text-align: center;
    }
    .time_last {
        font-size: .6rem;
        color: #666;
        margin-top: 1rem;
    }
    .show_time_amount .time{
        font-size: 1.5rem;
        color: #333;
        margin: .3rem 0 1rem;
    }
    .pay_way{
        background-color: #f1f1f1;
        padding: 0 .7rem;
        font-size: .7rem;
        color: #666;
        line-height: 1.8rem;
    }
    .pay_way_list{
        background-color: #fff;
    }
    .pay_way_list .pay_item    .pay_icon_contaienr, .pay_way_list .pay_item {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .color {
        color: #4cd964 !important;
    }
    .pay_icon_contaienr img {
        width: 2rem;
        height: 2rem;
        margin-right: .3rem;
    }
    .pay_item i {
        color: gray;
    }
    .determine{
        background-color: #4cd964;
        font-size: .7rem;
        color: #fff;
        text-align: center;
        margin: 0 .7rem;
        line-height: 1.8rem;
        border-radius: .2rem;
        margin-top: .5rem;
        font-weight: 700;
    }
    .pay_way_list .pay_item{
        padding: .4rem .7rem;
        line-height: 2.6rem;
        border-bottom: .025rem solid #f5f5f5;
    }
    .alet_container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 200;
    }
    .tip_text_container {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .tip_icon {
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .tip_icon span:first-of-type {
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .tip_icon span:nth-of-type(2) {
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .tip_text{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confrim{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
    .tip_text_container{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
</style>